import styled, { css } from "styled-components";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Title2, Title3, Text, InnerWrap } from "../../assets/styles/component";

import icon_bills from "../../assets/images/icon_bills.svg";
import icon_chat from "../../assets/images/icon_chat.svg";
import icon_shines from "../../assets/images/icon_shines.svg";
import icon_organization from "../../assets/images/icon_organization.svg";
import like from "../../assets/images/Like.svg";
import disLike from "../../assets/images/DisLike.svg";
import BadAd from "../../assets/images/BadAd.webp";
import BestAd from "../../assets/images/BestAd.webp";

import arrow from "../../assets/images/purpure_arrow.svg";
import { useEffect, useRef, useState } from "react";

const bg = css`
  content: "";
  z-index: -1;
  position: absolute;
`;

const SCompare = styled.section`
  position: relative;
  padding: 200px 0;

  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > p {
      text-align: center;
      margin-bottom: 141px;
    }

    &::after {
      ${bg}
      width: 43vw;
      height: 39vw;
      left: 60%;
      top: 40%;

      background: #ff429e;
      filter: blur(150px);
      transform: translate(-50%, -50%) rotate(39.54deg);
      border-radius: 50%;
      opacity: 0.4;

      mix-blend-mode: screen;
    }

    &::before {
      ${bg}
      width: 25vw;
      height: 39vw;
      left: 50%;
      top: 50%;

      background: #ff429e;
      filter: blur(400px);

      border-radius: 50%;
      transform: translate(-50%, -50%) rotate(-42.61deg);

      mix-blend-mode: screen;
    }
  }

  &::after {
    ${bg}
    width: 39vw;
    height: 39vw;
    left: 45%;
    top: 45%;
    border-radius: 50%;
    filter: blur(150px);
    opacity: 0.4;
    transform: translate(-50%, -50%);

    background: #f10275;
    mix-blend-mode: screen;
  }

  &::before {
    ${bg}
    width: 39vw;
    height: 39vw;
    left: 35%;
    top: 35%;
    background: #0202f1;

    opacity: 0.5;
    border-radius: 50%;
    filter: blur(150px);
    transform: translate(-50%, -50%);

    mix-blend-mode: screen;
  }

  @media (max-width: 1200px) {
    padding: 70px 0;

    & > div > p {
      margin-bottom: 70px;
    }
  }
`;

const Title = styled(Title2)`
  margin-bottom: 30px;
`;

const CompareBlock = styled.div`
  display: grid;
  grid-template-columns: 20% 3fr 20%;
  grid-template-rows: 1fr 1fr;
  margin-bottom: 227px;
  width: 100%;

  @media (max-width: 1300px) {
    grid-template-rows: 120px 1fr;
    gap: 20px;
    margin-bottom: 120px;
  }
`;

const Frame = styled.div`
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row: 1 / span 2;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  max-width: 999px;
  outline: 20px solid #ffffff22;
  border-radius: 70px;
  width: 100%;
  height: fit-content;
  max-width: 789px;
  min-height: 30vw;
  display: flex;
  justify-content: center;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
  }

  @media (max-width: 1000px) {
    border-radius: 24px;
    outline: 10px solid #ffffff22;

    & > img {
      border-radius: 23px;
    }
  }
`;

const sLike = css`
  display: flex;
  align-items: center;
  gap: 7px;
  width: fit-content;
  height: fit-content;
  font-size: 24px;
  color: #fff;
  z-index: 3;
`;

const DisLike = styled.div`
  ${sLike}
  padding: 15px 42px 15px 22px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.16);
  backdrop-filter: blur(11px);
  border-radius: 50px;
  transform: translate(32%, 50%) rotate(7.83deg);
  font-size: 25px;
  align-self: flex-start;
  justify-self: flex-end;

  @media (max-width: 1300px) {
    white-space: nowrap;
    transform: translate(32%, 20%) rotate(7.83deg) scale(0.5);
  }
`;

const Like = styled.div`
  ${sLike}
  grid-column-start: 2;
  grid-row-start: 2;
  padding: 15px 34px 15px 24px;
  background: radial-gradient(
      152.32% 158.02% at -13.45% 12.49%,
      #d83575 3.12%,
      #7e03f4 59.16%,
      #1010f2 100%
    ),
    #ffffff;
  box-shadow: inset 0px 0px 64.6636px rgba(255, 255, 255, 0.7);
  border-radius: 55px;
  transform: translateX(10%) rotate(-12.37deg);
  align-self: flex-end;
  justify-self: flex-end;
  position: relative;

  @media (max-width: 1300px) {
    transform: translate(30%, 60%) rotate(-8.37deg) scale(0.6);
  }

  @media (max-width: 1000px) {
    transform: translate(30%, 80%) rotate(-8.37deg) scale(0.5);
  }
`;

const Arrow = styled(LazyLoadImage)`
  position: absolute;
  right: -75%;
  top: -80%;
  transform: rotate(5deg);
`;

const List = styled.ul`
  display: flex;
  flex-direction: column;
  color: #fff;
  gap: 40px;
  max-width: 254px;
  grid-column-start: 3;
  grid-row-start: 1;
  justify-self: flex-end;

  ${({ mark }) =>
    !mark &&
    `
    grid-column-start: 1;
    grid-row-start: 2;
    justify-self: flex-start;
  `}

  li {
    position: relative;

    &:before {
      position: absolute;
      content: "";
      top: 5px;
      left: -38px;
      width: 10px;
      height: 10px;
      border-radius: 10px;
      background-color: ${({ mark }) => (mark ? mark : `#363636`)};

      @media (max-width: 1600px) {
        width: 8px;
        height: 8px;
        left: -15px;
      }
    }

    @media (max-width: 1600px) {
      font-size: clamp(10px, 1.2vw, 14px);
    }
  }

  @media (max-width: 1600px) {
    &:nth-child(4) {
      margin-left: 45px;
    }
  }

  @media (max-width: 1300px) {
    gap: 10px;
  }
`;

const BestList = styled.div`
  display: flex;
  justify-content: space-around;
  text-align: center;
  width: 100%;
  gap: 40px;

  @media (max-width: 1600px) {
    flex-wrap: wrap;
  }
`;

const Best = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1600px) {
    width: calc(50% - 40px);
  }

  h3 {
    margin-bottom: 17px;
  }

  p {
    opacity: 0.7;
  }
`;

const Icon = styled.div`
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: inset 0px 0px 64.6636px rgba(255, 255, 255, 0.51);
  border-radius: 51px;
  width: 150px;
  height: 150px;
  margin-bottom: 60px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: ${({ color }) => {
    switch (color) {
      case "blue":
        return `#0202F1;`;
      case "orange":
        return `#E3592F`;
      case "violet":
        return `#9C00E8`;
      case "yellow":
        return `#ECA356`;
    }
  }};

  @media (max-width: 1300px) {
    width: 74px;
    height: 74px;
    border-radius: 20px;

    img {
      width: 26px;
    }
  }
`;

const BackImg = styled(LazyLoadImage)`
  position: absolute;
  z-index: 1;
`;

const FrontImg = styled(LazyLoadImage)`
  position: absolute;
  left: -6%;
  top: 0;
  width: 100%;
`;

const Move = styled.div`
  z-index: 2;
  border-left: 1px solid #fff;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 6%;

  /* @media (max-width: 1300px) {
    width: 68px;
    height: 68px;
  } */
`;

const MoveBtn = styled.div`
  position: absolute;
  top: 50%;
  left: 0;
  user-select: none;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  cursor: pointer;

  width: 98px;
  height: 98px;

  border-radius: 50%;
  background-color: #fff;

  color: #111fcb;
  font-weight: 700;
  font-size: 11px;
  line-height: 140%;

  z-index: 3;
  transform: translateY(-50%);

  div {
    display: flex;
    gap: 11px;
    margin-top: 10px;

    svg:last-child {
      transform: rotate(180deg);
    }
  }

  @media (max-width: 1300px) {
    width: 68px;
    height: 68px;
  }
`;

const Compare = ({ language }) => {
  const [coordX, setCoordX] = useState(0);
  const [dragMv, setDragMv] = useState(false);

  useEffect(() => {
    if (coordX > 0) {
      MBtn.current.style.left = coordX - 500 + "px";
      Mv.current.style.left = coordX - 450 + "px";
      img.current.style.left = "-" + (coordX - 450) + "px";
    }
  }, [coordX]);

  const img = useRef(null);
  const MBtn = useRef(null);
  const Mv = useRef(null);

  return (
    <SCompare>
      <InnerWrap>
        <Title>Сравните сами</Title>
        <Text size="20">
          Вместо обычных ссылок вы сможете интегрировать неограниченное
          количество стримов уникальный промо-формат
        </Text>

        <CompareBlock>
          <DisLike>
            <LazyLoadImage src={disLike} alt="Dislike" loading="lazy" /> Old Fashion
          </DisLike>

          <List>
            <li>переход с видео (теряется ссылка, или контент)</li>
            <li>забывается после просмотра видео</li>
            <li>необходимо голосовое объявление</li>
            <li>
              замыленность формата <br /> (не реагируют на рекламу)
            </li>
          </List>

          <Frame
            onMouseMove={(event) => {
              if (dragMv) setCoordX(event.pageX);
            }}
          >
            <BackImg src={BadAd} alt="BackImg" loading="lazy" />
            <MoveBtn
              ref={MBtn}
              onMouseDown={() => {
                setDragMv(true);
              }}
              onMouseUp={() => {
                setDragMv(false);
              }}
            >
              Двигай
              <div>
                <svg
                  width="9"
                  height="15"
                  viewBox="0 0 9 15"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M8 0.999878L2 7.49988L8 13.9999"
                    stroke="#111FCB"
                    strokeWidth="2"
                  />
                </svg>
                <svg
                  width="9"
                  height="15"
                  viewBox="0 0 9 15"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M8 0.999878L2 7.49988L8 13.9999"
                    stroke="#111FCB"
                    strokeWidth="2"
                  />
                </svg>
              </div>
            </MoveBtn>
            <Move ref={Mv}>
              <FrontImg ref={img} src={BestAd} alt="BackImg" loading="lazy" />
            </Move>
          </Frame>

          <List mark="violet">
            <li>всегда на нужном месте не закрывают блоггера</li>
            <li>круто выглядят и привлекают внимание</li>
            <li>100% видимость, не нужнообъявлять голосом</li>
            <li>не отвлекает от просмотра контента</li>
          </List>

          <Like>
            <LazyLoadImage src={like} alt="" loading="lazy" /> SocialStream
            <Arrow src={arrow} alt="" loading="lazy" />
          </Like>
        </CompareBlock>

        <BestList>
          <Best>
            <Icon color="blue">
              <LazyLoadImage src={icon_chat} alt="Like" loading="lazy" />
            </Icon>

            <Title3>
              Больше <br /> вовлеченности
            </Title3>
            <Text size="16">
              и показатели для достижения <br /> brand awarnece
            </Text>
          </Best>

          <Best>
            <Icon color="orange">
              <LazyLoadImage src={icon_shines} alt="" loading="lazy" />
            </Icon>

            <Title3>
              Новизна формата <br /> и не замыленность
            </Title3>
            <Text size="16">
              это интересно, вирально, <br />
              уникально
            </Text>
          </Best>

          <Best>
            <Icon color="violet">
              <LazyLoadImage src={icon_organization} alt="" loading="lazy" />
            </Icon>

            <Title3>
              Неорганиченная <br /> маштабируемость
            </Title3>
            <Text size="16">
              ваша интеграция всегда <br /> в уместном формате и месте
            </Text>
          </Best>

          <Best>
            <Icon color="yellow">
              <LazyLoadImage src={icon_bills} alt="" loading="lazy" />
            </Icon>

            <Title3>
              Оплата за <br /> фактический охват
            </Title3>
            <Text size="16">
              вы получаете просмотры, <br /> а не факт интеграции
            </Text>
          </Best>
        </BestList>
      </InnerWrap>
    </SCompare>
  );
};

export default Compare;
